body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(242,242,242);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
    font-family: Calibri;
    text-align: center;
    margin-top: 1.5em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2.5vmin);
}

.Header-title {
  width: 90vh;
  font-weight: bold;
  background-color: rgb(197,224,179);
}

.Header-main-line {
  color: rgb(83,129,53);
  font-weight: bold;
  font-style: italic;
  font-size: calc(10px + 0.9vmin);
}

.Header-main-line sup{
  font-size: 10px;
}
.Details-container {
    margin-top: 1.5em;
    font-size: calc(10px + 0.6vmin);
}

.Details-item {
    font-size: calc(10px + 0.6vmin);
    display: inline;
    margin-right: 1em;
}

.Details-label {
    font-weight: bold;
}

.Details-red {
    color: red;
}
.Body {
    font-family: Calibri;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 1.5em;
}

.Body-header {
    width: 90vh;
    font-weight: bold;
    background-color: rgb(226,239,217);
    font-size: calc(10px + 0.9vmin);
    color: rgb(112,173,71);
    margin-bottom: 0.5em;
}
.Profile-attribute {
    font-size: calc(10px + 0.5vmin);
    margin-bottom: 1em;
}

.Profile-attribute-name {
    display: inline-block;
    vertical-align: top;
    width: 20vh;
    font-weight: bold;
}

.Profile-attirbute-description {
    display: inline-block;
}

.Profile-attirbute-description-line {
    display: block;
}
.Skill-board {
    display: inline-block;
    width: 34vh;
    margin-left: 2em;
    margin-right: 2em;
    margin-bottom: 0.5em;
    font-family: Century Gothic;
    font-size: 13px;
}

.Skill-item {
    display: inline-block;
    width: 35vh;
}

.Skill-item-stars{
    float: right;
}

.Skill-green-star {
    color: rgb(83,129,53);
}

.Skill-grey-star {
    color : rgb(165,165,165);
}

.Position {
    font-size: 14px;
    margin-top: 1em;
}

.Position-title {
    font-weight: bold;
}

.Position-dates {
    font-weight: bold;
    font-style: italic;
    color: rgb(128,128,128);
}
.Education-item {
    font-size: 14px;
    margin-top: 1em;
}

.Education-item-title {
    font-weight: bold;
}

.Education-item-title-warning {
    color: red;
}
.Military-section {
    font-size: 14px;
}
